<template>
<div style="">
      <el-form v-model='getCarForm'>
      <el-row>
        <el-col :span='5'>
           <el-form-item label='车场名称' class='laeblStyle'>
              <el-select v-model='getCarForm.parkingId' class='handle-input' v-show='parkingIdOptionsShow' clearable>
                  <el-option
                  v-for='item in parkingIdOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
              </el-select>
           </el-form-item>
        </el-col>
        <el-col :span='5'>
          <el-form-item label='车辆分类' class='laeblStyle'>
              <el-select v-model='getCarForm.vehicleType' class='handle-input'  v-if='vehicleTypeShow' clearable>
                <el-option
                v-for='item in vehicleTypeIdOptions'
                :key='item.id'
                :label='item.label'
                :value='item.value'>
                </el-option>
              </el-select>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span='2'>
          <el-button type="primary" @click="getCarMsg()" >查询</el-button>
        </el-col>
      </el-row>
  </el-form>
    <!-- 第二个表格 -->
 <el-table
      :data="tableCar"
      style="width: 100%;padding:10px 20px">
      <!-- <el-table-column
      prop="id"
      label="序号"
      width='100'>
    </el-table-column> -->
      <el-table-column
        prop="parkingName"
        label="车场名称">
      </el-table-column>
      <el-table-column
        prop="passRuleName"
        label="通行规则名称">
      </el-table-column>
      <el-table-column
        prop="vehicleClassifyName"
        label="车辆分类">
      </el-table-column>
      <el-table-column
        prop="feeRuleName"
        label="费率表">
      </el-table-column>
      
      <!-- <el-table-column
        prop="startTime"
        label="周">
      </el-table-column> -->
      <!-- 操作 -->
      <el-table-column
      label="操作" width="260px">
      <template slot-scope="scope">
          <el-button
              @click="handleDetailMsg(scope.$index, scope.row,scope)"
              type="primary"
              size="small">详情
          </el-button>
          <el-button
              @click="handleChangeMsg(scope.$index, scope.row,scope)"
              type="primary"
              size="small">编辑
          </el-button>
          <el-button
              @click="handleDelMsg(scope.$index, scope.row)"
              type="primary"
              size="small">删除
          </el-button>
      </template>
      </el-table-column>
    </el-table> 
    <!-- 分页条 -->
    <!-- page-sizes, 每页显示条数   
        :page-size 当前页显示条数   feeSystemTradeNo
     -->
    <el-pagination
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="total" v-show='paginationShow' 
        style='margin-top:10px;'
        :page-sizes="[5,10,20,30]"      
        :page-size="pageSize"
        @size-change="handleSizeChange"
        @current-change="currentChange"
        :current-page="currentPage">
    </el-pagination>
    <!-- 模态框 -->
    <el-dialog title="详细信息" 
    :visible.sync="dialogVisible" style='' width='80%'>
        <el-form v-model='detailForm'>
            <el-card class='box-card box-card-ex'>
              <el-row>
                 <el-col :span='7'>
                    <el-form-item label='车场名称' style='width:300px;'>
                        <el-input v-model='detailForm.parkingName' style="width:70%;margin-left:20px;">
                        </el-input>
                     </el-form-item>
                 </el-col>
                 <el-col :span='7'>
                    <el-form-item label='规则名称' style='width:300px;'>
                      <el-input v-model='detailForm.passRuleName' style="width:70%;margin-left:20px;">
                      </el-input>
                  </el-form-item>
                 </el-col>
                 <el-col :span='7'>
                    <el-form-item label='车辆类型' style='width:300px;'>
                      <el-input v-model='detailForm.vehicleTypeName' style="width:70%;margin-left:20px;">
                      </el-input>
                  </el-form-item>
                 </el-col>
            </el-row>
              <!-- 第二行 -->
             <el-row>
               <el-col :span='7'>
                  <el-form-item label='设施名称' style='width:300px;'>
                    <el-input type='textarea' :rows='2' v-model='detailForm.facility' style="width:70%;margin-left:20px;">
                    </el-input>
                  </el-form-item>
               </el-col>
               <el-col :span='7'>
                  <el-form-item label='时间名称' style='width:300px;'>
                    <el-input type='textarea' :rows='2' v-model='detailForm.timeName' style="width:70%;margin-left:20px;">
                    </el-input>
                  </el-form-item>
               </el-col>
               <el-col :span='4'>
                  <el-form-item label='车牌颜色' style='width:180px;'>
                    <el-input v-model='detailForm.plateColor' class='small-input'></el-input>
                  </el-form-item>
               </el-col>
               <el-col :span='4'>
                  <el-form-item label='核载人数' style='width:240px;'>
                      <el-input v-model='detailForm.hzrs' style='width:60%'></el-input>
                    </el-form-item>
               </el-col>
             </el-row>
             <!-- 第三行 -->
             <el-row>
                <el-col :span='7'>
                  <el-form-item label='许可类型' style='width:300px;'>
                    <el-input v-model='detailForm.allowType' style="width:50%;margin-left:-40px;">
                    </el-input>
                  </el-form-item>
                </el-col>
                <el-col :span='7'>
                    <el-form-item label='通行类型' style='width:300px;'>
                      <el-input v-model='detailForm.passType' style="width:50%;margin-left:-40px;">
                      </el-input>
                    </el-form-item>
                  </el-col>
                <el-col :span='4.5'>
                    <el-form-item label='尾号类型' style='width:200px;'>
                      <el-input v-model='detailForm.tailNumType' style="width:50%;"></el-input>
                    </el-form-item>
                  </el-col>
                <el-col :span='4'>
                    <el-form-item label='尾号' style='width:200px;'>
                        <el-input type='textarea' :rows='2' v-model='detailForm.timeName' style="width:70%;margin-left:20px;">
                        </el-input>
                      </el-form-item>
                  </el-col>
             </el-row>
             <!-- 第四行 -->
             <el-row>
                <el-col :span='7'>
                    <el-form-item label='收费类型' style='width:300px;'>
                      <el-input v-model='detailForm.feeType' style="width:50%;margin-left:-40px;">
                      </el-input>
                    </el-form-item>
                </el-col>
                  <!-- 车辆分类 -->
                  <el-col :span='7'>
                      <el-form-item label='车辆分类' style='width:300px;'>
                        <el-input v-model='detailForm.vehicleClassifyName' style="width:50%;margin-left:-40px;">
                        </el-input>
                      </el-form-item>
                  </el-col>
                  <!-- 收费规则名称 -->
                  <el-col :span='7'>
                      <el-form-item label='收费规则名称' style='width:320px;'>
                          <el-input v-model='detailForm2.feeRuleId' style="width:60%;margin-left:20px;">
                          </el-input>
                      </el-form-item>
                  </el-col>
             </el-row>
            </el-card>
        </el-form>
      
    </el-dialog>
       <!-- 编辑模态框 -->
       <el-dialog title="编辑信息" 
       :visible.sync="dialogVisible2" style='' width='80%'>
           <el-form v-model='detailForm2'>
            <!-- 复制详情信息 -->
            <el-card class='box-card box-card-ex'>
                <el-row>
                   <el-col :span='7'>
                      <el-form-item label='车场名称' style='width:300px;'>
                          <el-input v-model='detailForm2.parkingName' style="width:70%;margin-left:20px;">
                          </el-input>
                       </el-form-item>
                   </el-col>
                   <el-col :span='7'>
                      <el-form-item label='规则名称' style='width:300px;'>
                        <el-input v-model='detailForm2.passRuleName' style="width:70%;margin-left:20px;">
                        </el-input>
                    </el-form-item>
                   </el-col>
                   <el-col :span='7'>
                      <el-form-item label='车辆类型' style='width:300px;'>
                        <el-input v-model='detailForm2.vehicleTypeName' style="width:70%;margin-left:20px;">
                        </el-input>
                    </el-form-item>
                   </el-col>
  
              </el-row>
                <!-- 第二行 -->
               <el-row>
                 <el-col :span='7'>
                    <el-form-item label='设施名称' style='width:300px;'>
                      <el-input type='textarea' :rows='2' v-model='detailForm2.facility' style="width:70%;margin-left:20px;">
                      </el-input>
                    </el-form-item>
                 </el-col>
                 <el-col :span='7'>
                    <el-form-item label='时间名称' style='width:300px;'>
                      <el-input type='textarea' :rows='2' v-model='detailForm2.timeName' style="width:70%;margin-left:20px;">
                      </el-input>
                    </el-form-item>
                 </el-col>
                 <el-col :span='4'>
                    <el-form-item label='车牌颜色' style='width:180px;'>
                      <el-input v-model='detailForm2.plateColor' class='small-input'></el-input>
                    </el-form-item>
                 </el-col>
                 <el-col :span='4'>
                    <el-form-item label='核载人数' style='width:240px;'>
                        <el-input v-model='detailForm2.hzrs' style='width:60%'></el-input>
                      </el-form-item>
                 </el-col>
               </el-row>
               <!-- 第三行 -->
               <el-row>
                  <el-col :span='7'>
                    <el-form-item label='许可类型' style='width:300px;'>
                      <el-input v-model='detailForm2.allowType' style="width:50%;margin-left:-40px;">
                      </el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span='7'>
                      <el-form-item label='通行类型' style='width:300px;'>
                        <el-input v-model='detailForm2.passType' style="width:50%;margin-left:-40px;">
                        </el-input>
                      </el-form-item>
                    </el-col>
                  <el-col :span='4.5'>
                      <el-form-item label='尾号类型' style='width:200px;'>
                        <el-input v-model='detailForm2.tailNumType' style="width:50%;"></el-input>
                      </el-form-item>
                    </el-col>
                  <el-col :span='4'>
                      <el-form-item label='尾号' style='width:200px;'>
                          <el-input type='textarea' :rows='2' v-model='detailForm2.timeName' style="width:70%;margin-left:20px;">
                          </el-input>
                        </el-form-item>
                    </el-col>
               </el-row>
               <!-- 第四行 -->
               <el-row>
                  <el-col :span='7'>
                      <el-form-item label='收费类型' style='width:300px;'>
                        <el-input v-model='detailForm2.feeType' style="width:50%;margin-left:-40px;">
                        </el-input>
                      </el-form-item>
                  </el-col>
                    <!-- 车辆分类 -->
                    <el-col :span='7'>
                        <el-form-item label='车辆分类' style='width:300px;'>
                          <el-input v-model='detailForm2.vehicleClassifyName' style="width:50%;margin-left:-40px;">
                          </el-input>
                        </el-form-item>
                    </el-col>
                    <!-- 收费规则名称 -->
                    <el-col :span='7'>
                      <el-form-item label='收费规则名称' style='width:320px;'>
                        <el-input v-model='detailForm2.feeRuleId' style="width:60%;margin-left:20px;">
                        </el-input>
                    </el-form-item>
                    </el-col>
               </el-row>
                 <!-- 按钮行 -->
						<div style='float:right;margin:10px 15% 0 0;'>
								<el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel()'>取消</el-button>
							<el-button class="el-button el-button--primary" style='width:80px' @click='changeMonthly(detailForm2)'>保存</el-button>
						</div>
            <div style="clear:both"></div>
              </el-card>

           </el-form>
       </el-dialog>
        
        </el-form>
       </el-dialog>
</div>
</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  export default {
    methods: {
      //删除 
      handleDelMsg(index,row){
        console.log(index)
        console.log(row)
        var self=this;
        this.$confirm('确认继续吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          //调用接口
          var url =self.baseUrl+"passRule/delete?id="+row.id;
          self.$axios.get(url).then(function(res){
            console.log(res.data)
            if(res.data.status=='0'){
              self.$message({
                  type: 'success',
                  message:res.data.message
              });
              self.getCarMsg();
            }else{
              self.$message({
                  type: 'info',
                  message:res.data.message
              });
            }
          }).catch(err=>{console.log(err)})

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      },
      //增删 日期输入框
      remove(item,index){
        console.log("看下索引")
        var i=this.addRuleForm.sjForm.indexOf(item)
        console.log(i)

      },
      addTimeForm(){
        this.addRuleForm.sjForm.push({
          startTime:"",
          endTime:"",
        })
      },
      //日期变化,
      handleCheckAllChange(val){
        console.log("myVal"+val)
        this.checkedItems=val?this.weekOptions:[]
        this.isIndeterminate=false;
      },
      handleCheckedItems(val){
        console.log("val")
        console.log(val)
        let checkedCount = val.length;
        this.checkAll=checkedCount ==this.weekOptions.length;
        this.isIndeterminate=checkedCount>0&&checkedCount<this.weekOptions.length
      },
      //监听时间类型变化
      addTimeType(val){
        var self=this;
        console.log("val")
        console.log(val)
        if(val=='1'){
          self.tiemValAdd=true;
        }else{
          self.tiemValAdd=false;
        }
      },
      //取消按钮
      addCancel(){
        this.dialogVisibleAdd=false;
      },
      //规则添加
      addRule(val){

      },
      //新增通行规则
      addCarMsg(){
        var self=this;
        self.dialogVisibleAdd=true;
      },
      //获取时间类型
      changTimeType(val){
        console.log("val")
        console.log(val)
        var self=this;

      },
      //获取车辆类型
      getCarType(){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/initVehicleClassify"
        self.$axios.get(url).then(function(res){
          console.log("res..通行规则")
          console.log(res.data.data)
          self.vehicleTypeShow=true;
          for(var i=0;i<res.data.data.length;i++){
            self.vehicleTypeIdOptions[i]={
              value:res.data.data[i].id,
              label:res.data.data[i].vehicleClassifyName,
              id:res.data.data[i].id
            }
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      //修改通行规则
      changeMonthly(val){
        console.log(val)
        var self=this;
        var url=self.baseUrl+"passRule/update";
        var params={
          "id":val.id,
          "parkingId":val.parkingId,
          "plateNo":val.palteNo,
          "passRuleName":val.passRuleName,
          "timeId":val.timeName,
          "facility":val.facility,
          "vehicleClassifyId":val.vehicleClassifyName,
          "plateColorNo":val.plateColor,
          "vehicleTypeId":val.feeRuleName,
          "vehicleHzrsId":val.hzrs,
          "allowType":val.allowType,
          "passTypeId":val.passType,
          "feeType":val.feeType,
          "feeRuleId":val.feeRuleId,
          "plateFeature":{
            "tailNumType":val.tailNumType,
            "tailNum":val.timeName
          }
        }
        self.$axios.post(url,params).then(function(res){
          console.log("修改的返回值")
          console.log(res.data)
        if(res.data.status=='0'){
          self.$message({
            message:res.data.message,
            type:"info"
          })
          self.dialogVisible2=false;
        }
        }).catch(err=>console.log(err))
      },
      //月租类型
      getMonthly(){
        var self=this;
        var url=self.baseUrl+'vehicleMonthly/initMonthly';
        self.$axios.get(url).then(function(res){
          console.log("月租类型res")
          console.log(res.data)
          for(var i=0;i<res.data.data.length;i++){
            self.monthlyTypeNoOptions[i]={
              value:res.data.data[i].monthlyTypeNo,
              label:res.data.data[i].monthlyType
            }
          }
        }).catch(err=>console.log(err))
      },
      cancel(){
        this.dialogVisible2=false;
      },
      getParkingId(){
        var self=this;
			var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId");
			self.$axios.get(url).then(function(res){
				self.parkingIdOptionsShow=true;
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
      },
      axiosChangeCar(params){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/updateVehicle";
        self.$axios.post(url,params).then(function(res){
          console.log('res.data.......');
          console.log(res.data);
        }).catch(function(err){
          console.log(err);
        })              
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url=self.baseUrl +"vehicleMonthly/list"
            var paramString={
              'plateNo':this.inputCph,
              'currentPage':this.currentPage,
              'pageSize':this.pageSize,
              "parkingId":this.getCarForm.parkingId

            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          var url=self.baseUrl +"vehicleMonthly/list"
          var str={
            'plateNo':this.inputCph,
              'currentPage':1,
              'pageSize':this.pageSize,
              "parkingId":this.getCarForm.parkingId
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(params){
        console.log("params")
        console.log(params)
        var self=this;
        var url=self.baseUrl+"passRule/list"
        this.paginationShow=true;
        this.$axios({
          method:"post",
          url:url,
          headers:{
            "Content-Type":"application/json"
          },
          data:params
        }).then(function(response){
          self.total=response.data.data.total;
          self.tableCar=response.data.data.records;
          for(var i=0;i<self.tableCar.length;i++){
            // self.tableCar[i].startTime=self.momentTime1(self.tableCar[i].startTime);
            // self.tableCar[i].endTime=self.momentTime1(self.tableCar[i].endTime);
            self.tableCar[i].timeTypeId=='1'?self.tableCar[i].timeTypeId='时间段':
            self.tableCar[i].timeTypeId='周'
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime1(val){
          return moment(val).format("YYYY-MM-DD");
      },
      momentTime2(val){
          return moment(val).format("HH:mm:ss");
      },
      getCarMsg(){
        var self=this;
          var url=self.baseUrl +"passRule/list"
          //查询，获取参数
          var str={
            'parkingId':self.getCarForm.parkingId,
            "currentPage":1,
            "pageSize":10,
            "vehicleClassifyId":self.getCarForm.vehicleType
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(jsonParams);
      },
      //查看详情;
      handleDetailMsg(index,row,scope){
        var self=this;
        this.dialogVisible=true;
        //调用回显接
        var url=self.baseUrl+"passRule/detail?id="+row.feeRuleId;
        self.$axios.get(url).then(function(res){
          if(res.data.status=='0'){
            console.log("看下响应值")
            console.log(res.data)
            self.detailForm=res.data.data;
            self.detailForm.plateColor=res.data.data.plateColor.plateColor;
            self.detailForm.passType=res.data.data.passType.passType;
            self.detailForm.feeType=res.data.data.feeType.feeType;
          }else{
            self.$message({
              message:"请求失败,请重试!",
              type:"info"
            })
          }
        }).catch(err=>console.info(err));
      },
      handleChangeMsg(index,row,scope){
        var self=this;
        self.dialogVisible2=true;
        //调用回显-显示
        var url=self.baseUrl+"passRule/detail?id="+row.feeRuleId;
        self.$axios.get(url).then(function(res){
          if(res.data.status=='0'){
            self.detailForm2=res.data.data;
          }else{
            self.$message({
              message:"请求失败,请重试!",
              type:"info"
            })
          }
        }).catch(err=>console.info(err));
      }
    },
    data() {
      return {
        isIndeterminate:true,
        weekAdd:true,
        tiemValAdd:false,
        addRuleForm:{
          sjForm:[
            
          ]
        },
        dialogVisibleAdd:true,
        timeTypeVal:false,
        vehicleTypeShow:false,
        vehicleTypeIdOptions:[],
        getCarForm:{},
          inputCph:'',
          paginationShow:false,
          tableShow:[],
          tableCar:[],
          total:0,
          currentPage:2,
          pageSize:10,
          dialogVisible:false,
          row:[],
          imgRul2:'',
          zfVal:'',
          zfOptions:[
            {id:1,label:"已支付",value:"true"},
            {id:2,label:"未支付",value:"false"}
          ],
          tiemVal:'',
          detailForm:{},
          detailType:false,
          detailType2:false,
          dialogVisible2:false,
          detailForm2:{},
          parkingIdOptionsShow:false,
          parkingIdOptions:[],
          vehicleTypeOptionsShow:false,
          vehicleTypeOptions:[],
          plateColorOptions:[],
          plateColorOptionsShow:false,
          isReletOptions:[
            {id:0,value:0,label:"否"},
            {id:1,value:1,label:"是"},
          ],
          monthlyTypeNoOptions:[],
          timeTypeOption:[
            {id:1,value:1,label:"日期"},
            // {id:2,value:2,label:"星期"},
            {id:3,value:3,label:"假期"},
          ],
          weekOptions:[
            "周一","周二","周三","周四","周五","周六","周日"
          ],
          indeterminate:true,
          checkAll:false,
          checkedItems:'',
      }
    },
    mounted:function(){
        this.getParkingId();
        this.getMonthly();
        this.getCarMsg();
        this.getCarType();
        // this.getCarColor();
    }
  }
</script>
